import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Systemhomepage from '@/components/Systemhomepage'
import Checkstatus1 from '@/components/Checkstatus1'
import Checkstatus11 from '@/components/Checkstatus11'
import Checkstatus from '@/components/Checkstatus'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path:'/Checkstatus',
      name:' Checkstatus',
      component:  Checkstatus
    },
    {
      path:'/Checkstatus1',
      name:' Checkstatus1',
      component:  Checkstatus1
    },
    {
      path:'/Checkstatus11',
      name:' Checkstatus11',
      component:  Checkstatus11
    },
    {
      path:'/Systemhomepage',
      name:' Systemhomepage',
      component: Systemhomepage
    }
  ]
})



